.container {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 10px;
    border: 1px solid transparent;
    box-sizing: border-box;
}

.wrapper{
    position: relative;
}

.label {
    position: absolute;
    transition: all .3s ease;
    top: calc(50% - 9px);
    font-size: 16px;
    left: 5px;
    color: rgba(0, 0, 0, 0.2);
}

.labelFocused {
    font-size: 14px;
    left: 5px;
    color: rgba(0, 0, 0, 0.6);
    transform: translateX(-5px) translateY(-28px) scale(.9);
}

.input {
    width: 100%;
    padding: 10px;
    border: none;
    background: #fff;
    border-radius: 6px;
    box-sizing: border-box;
}


.inputError {
    border: 1px solid #fb6e6e;
}

.error{
    font-size: 14px;
    left: 5px;
    top: calc(100% + 6px);
    color: #fb6e6e;
}

.input::-webkit-outer-spin-button,
.input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.input[type=number] {
    -moz-appearance: textfield;
}
